"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import Image from "next/image";

/* --- G20 countries with ISO code mapping --- */
const COUNTRIES = [
  { code: "US", name: "United States" },
  { code: "CN", name: "China" },
  { code: "JP", name: "Japan" },
  { code: "DE", name: "Germany" },
  { code: "IN", name: "India" },
  { code: "GB", name: "United Kingdom" },
  { code: "FR", name: "France" },
  { code: "BR", name: "Brazil" },
  { code: "IT", name: "Italy" },
  { code: "CA", name: "Canada" },
  { code: "RU", name: "Russia" },
  { code: "AU", name: "Australia" },
  { code: "KR", name: "South Korea" },
  { code: "SA", name: "Saudi Arabia" },
  { code: "MX", name: "Mexico" },
  { code: "ID", name: "Indonesia" },
  { code: "ZA", name: "South Africa" },
  { code: "AR", name: "Argentina" },
  { code: "TR", name: "Turkey" },
  { code: "EU", name: "European Union" },
];

export default function CrashWatch() {
  const router = useRouter();
  const [scores, setScores] = useState<Record<string, number>>({});

  /* Simulate sentiment / crash-risk values */
  useEffect(() => {
    const obj: Record<string, number> = {};
    COUNTRIES.forEach(c => (obj[c.code] = Math.floor(Math.random() * 100)));
    setScores(obj);
  }, []);

  /* Sort worst (high risk) ‚Üí best (low risk) */
  const ranked = [...COUNTRIES].sort(
    (a, b) => (scores[b.code] ?? 0) - (scores[a.code] ?? 0)
  );

  const colorFor = (v: number) =>
    v > 70 ? "bg-red-500" : v > 50 ? "bg-amber-500" : "bg-blue-500";

  return (
    <div className="rounded-lg border bg-white p-3">
      <h2 className="text-xs uppercase tracking-wider text-slate-500 mb-2">
       ‚ö†Ô∏è Crash Watch
      </h2>
      <div className="max-h-72 overflow-y-auto divide-y">
        {ranked.map(c => {
          const score = scores[c.code] ?? 0;
          const flag = `https://flagcdn.com/24x18/${c.code.toLowerCase()}.png`;
          return (
            <button
              key={c.code}
              onClick={() => router.push(`/intel/${c.code}`)}
              className="w-full flex items-center justify-between gap-3 py-2 px-1 hover:bg-slate-50 transition text-left"
            >
              <div className="flex items-center gap-2">
                <Image
                  src={flag}
                  alt={c.name}
                  width={24}
                  height={18}
                  className="rounded-sm border bg-slate-100"
                  unoptimized
                />
                <span className="text-sm text-slate-700">{c.name}</span>
              </div>
              <div className="flex items-center gap-1">
                <div
                  className={`w-2 h-2 rounded-full ${colorFor(score)}`}
                  title={`Score ${score}`}
                />
                <span className="text-xs text-slate-500 w-8 text-right">
                  {score}
                </span>
              </div>
            </button>
          );
        })}
      </div>
    </div>
  );
}
